<template>
  <button
    :class="{ active: active }"
    @mousedown="handleClick"
    :title="props.label"
    :id="props.id"
  >
    <IconSvg
      :width="20"
      :height="20"
      :path="props.icon"
      :iconColor="'var(--white)'"
    />
  </button>
</template>

<script setup lang="ts">
import IconSvg from "@/DesignSystem/Icons/IconSvg.vue";
/**
 * Callback function for this button
 */
const props = defineProps({
  /**
   * Callback function on click
   */
  callback: {
    required: true,
    type: Function,
  },
  /** SVG path */
  icon: String,
  /** Add a class active on the button*/
  active: Boolean,
  /** Display on hover */
  label: String,
  id: String,
});

const handleClick = (event: Event) => {
  props.callback();
};
</script>

<style scoped>
.active {
  background-color: #424242;
  border: 2px solid #bababa;
  border-radius: 5px;
}
button {
  box-sizing: border-box;
  padding: 5px;
  font-size: 18px;
  background-color: #535353;
  color: #fff;
  border: none;
  width: 33px;
  height: 33px;
  transition: background-color 0.9s ease;
}

button:hover {
  background-color: #424242;
}
</style>

<script></script>
